.selmaWalk {
    position: absolute;
    transform: translate(50%,-50%);
    transform: scale(-1,1);
	width: calc(284px / 6);
	background: url(img/selma.png);
    animation: animate3 1s steps(6) infinite reverse;
}

@keyframes animate3 {
	from
	{
		background-position: 0;	
	}
	to
	{
		background-position: 284px;	
	}
}